// pages/arefundRefund/arefundRefund.js
var util = require("../../../../utils/util.js");

export default {
  data() {
    return {
      ordersForGoods: [],
      time: ""
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    var that = this;
    getApp().globalData.initPage(that);
    var id = getApp().globalData.userInfo.userId;
    console.log(options);
    let oid = JSON.parse(options.oid);

    uni.request({
      url: getApp().globalData.url + '/order/orderSelectByIOpayStatus?oId=' + oid + '&' + 'o_customerInfo=' + id,
      data: {},
      header: {
        'content-type': 'application/json'
      },
      method: 'POST',
      dataType: 'json',
      success: function (res) {
        that.setData({
          ordersForGoods: res.data.data
        });
        console.log(res.data);
      }
    }); // 调用函数时，传入new Date()参数，返回值是日期和时间


    var time = util.formatTime(new Date()); // 再通过setData更改Page()里面的data，动态更新页面的数据

    this.setData({
      time: time
    });
  },

  methods: {
    back: function () {
      uni.navigateBack({
        delta: 1
      });
    },
    fanhui: function () {
      var pages = getCurrentPages(); //获取页面栈

      if (pages.length > 1) {
        //上一个页面实例对象
        var prePage = pages[pages.length - 2]; //调用上一个页面的onShow方法

        prePage.onShow();
      }
    },
    setData: function (obj, callback) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
      callback && callback();
    }
  }
};
